.carousel {
  overflow: hidden;
  border-radius: 25px 25px 0 0;
  position: relative;
  height: 817px;
}

.inner {
  white-space: nowrap;
  transition: transform 0.3s;
}

.carousel-item {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 547px;  
  width: 100%;
}

.carousel-item:after {
  content:'';
  position:absolute;
  left:0; top:0;
  width:100%;
  height:100%;
  display:inline-block;
  background: -moz-linear-gradient(top, rgba(27, 27, 31, 0) 0%, #1B1B1F 65%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#1B1B1F), color-stop(65%,rgba(27, 27, 31, 0))); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, rgba(27, 27, 31, 0) 0%,#1B1B1F 65%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top, rgba(27, 27, 31, 0) 0%,#1B1B1F 65%); /* Opera 11.10+ */
  background: -ms-linear-gradient(top, rgba(27, 27, 31, 0) 0%,#1B1B1F 65%); /* IE10+ */
  background: linear-gradient(to bottom, rgba(27, 27, 31, 0) 0%,#1B1B1F 65%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
}

.carousel-item img{  
  width: 100%;
}

.carousel__text {
  position: absolute;
  top: 50%;
  width: 100%;
}

.indicators {
  display: flex;
  justify-content: space-between;
  margin: 12px;
}

.indicators > button {
  margin: 5px;
}
