  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.page a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: rgb(255, 255, 255);
}

* {
  box-sizing: border-box;
}

.flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.flex-between {
  justify-content: space-between;
}

.flex-around {
  justify-content: space-around;
}

.flex-evenly {
  justify-content: space-evenly;
}

.flex-center {
  justify-content: center;
}

.flex-align-center {
  align-items: center;
}

.flex-wrap {  
  flex-wrap: wrap;
}

.flex-grow-1 {
  flex-grow: 1;
}

.header {
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding: 17px 24px;
  background: url(../../static/media/lines.753a1db3.png);
  background-color: #1E1E1E;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

.header__logo {
  max-width: 130px;
  margin-left: 8px;
  width: 100%;
  height: 150px;
}

.header_navigation {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.header__menu {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style-type: none;
}

.header__menu-item {
  text-align: center;
  font-size: 20px;
  margin-right: 40px;
}

.header__menu-item a:hover {
  color: rgb(255, 255, 255);
  font-weight: bold;
}

.header__button {
  width: 30px;
  height: 30px;
  padding: 0;
  margin-right: 30px;
  cursor: pointer;
}

ul[role="listbox"] {
  color: #000;
}

.header__lang button {
  color: rgb(234, 236, 239);
  border: 0;
}

@media screen and (max-width: 880px) {
  .header {
    flex-direction: column;
    position: initial;
  }

  .header__menu {
    flex-direction: column;
    padding: 0;
  }

  .header__menu-item {
    margin-bottom: 10px;
  }
}

.content {
  margin: 0 auto;
  min-height: 50vh;
  max-width: 1280px;
}

@media screen and (max-width: 880px) {
  .content {
    margin: 0 19px;
  }
}

